@tailwind base;
@tailwind components;
@tailwind utilities;
@import "~slick-carousel/slick/slick.css";
@import "~slick-carousel/slick/slick-theme.css";

.backdrop {
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.061);
  position: fixed;
  top: 0;
  left: 0;
}

input:focus {
  outline: none !important;
}

input {
  border: none !important;
}
